import React from 'react';
import { arrayOf, bool, func, oneOf, shape, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';
import { ComboboxUtil } from '@visa/ui-utils';
const CSS_PREFIX = 'vds-listbox';

const renderListboxItem = (props, optionsTemplate) => {
  const {
    filterFunction,
    getItemProps,
    inputValue,
    listboxItemRef,
    noMatchCallback,
    noSelectionCheckMark,
    type,
    selectedItem
  } = props;

  const highlightedIndex = props.highlightedIndex < 0 ? -1 : props.highlightedIndex;
  const { itemKey, optionValues, renderOption } = optionsTemplate;
  let filteredOptions;

  if (type !== 'none' && inputValue && inputValue.trim()) {
    filteredOptions = ComboboxUtil.filterOptionValues(
      inputValue
        .trim()
        .replace(/\s\s+/g, ' ')
        .toLowerCase(),
      optionsTemplate,
      filterFunction
    );
  } else {
    filteredOptions = { count: optionValues ? optionValues.length : 0, itemValues: optionValues };
  }

  const filterCount = filteredOptions.count;
  const filteredValues = filteredOptions.itemValues;
  const highlightedItem = filteredOptions.itemValues ? filteredOptions.itemValues[highlightedIndex] : null;
  if (filterCount === 0) {
    noMatchCallback(true);
    return null;
  }
  noMatchCallback(false);

  return filteredValues.map((item, index) => {
    const isSelected = selectedItem && selectedItem[itemKey] === item[itemKey];
    const isHighlight = highlightedItem && highlightedItem[itemKey] === item[itemKey];
    const itemProps = {
      className: classnames(
        `${CSS_PREFIX}-item`,
        { [`${CSS_PREFIX}--item-selected`]: isSelected && !noSelectionCheckMark },
        { [`${CSS_PREFIX}--item-highlight`]: isHighlight },
        'visa-ui-btn-icon--light-fill'
      )
    };
    if (isSelected) {
      itemProps.ref = listboxItemRef;
    }
    return (
      <li
        {...getItemProps({
          index,
          item,
          key: item[itemKey]
        })}
        {...itemProps}
      >
        <span className={`${CSS_PREFIX}--item-container`}>
          {renderOption(item)}
          {isSelected && !noSelectionCheckMark ? <Icon name="checkmark" resolution="tiny" /> : null}
        </span>
      </li>
    );
  });
};

const ListBox = props => {
  const { getMenuProps, isOpen, listboxClass, isListboxOpen, listboxProps, listboxRef, optionsTemplate, ...restOfProps } = props;
  return (
    <div className={classnames('vds-menu--absolute ', { [`vds-menu--show`]: isOpen && isListboxOpen })}>
      <ul
        {...listboxProps}
        {...getMenuProps({
          className: classnames(CSS_PREFIX, listboxClass),
          ref: listboxRef
        })}
      >
        {isOpen && optionsTemplate && optionsTemplate.optionValues && optionsTemplate.optionValues.length ? renderListboxItem(restOfProps, optionsTemplate) : null}
      </ul>
    </div>
  );
};

ListBox.propTypes = {
  filterFunction: func,
  getMenuProps: func,
  isListboxOpen: bool,
  isOpen: bool,
  listboxClass: string,
  listboxItemRef: func.isRequired,
  listboxProps: shape({}),
  listboxRef: func.isRequired,
  optionsTemplate: shape({
    itemKey: string.isRequired,
    optionValues: arrayOf(shape({ value: string.isRequired })),
    renderOption: func.isRequired,
    searchIncludeFields: arrayOf(string)
  }),
  type: oneOf(['none', 'manual', 'auto'])
};

ListBox.displayName = 'ListBox';

export default ListBox;
