import React, { useEffect, useRef, useState } from 'react';
import classnames from 'classnames';
import Downshift from 'downshift';
import { arrayOf, bool, func, number, oneOfType, shape, string } from 'prop-types';
import Listbox from '../combobox/listbox';
import Input from '../input';
import useScrollToItem from '../combobox/useScrollToItem';
import useComboboxAction from '../combobox/useComboboxAction';

const ComboboxFreeformManualSelection = ({
  comboboxClass,
  comboboxAction,
  containerProps,
  errorText,
  // eslint-disable-line no-unused-vars
  inputProps,
  itemToStringFunc,
  onInputBlur,
  onInputChange,
  onInputFocus,
  onSelectionChange,
  optionsTemplate,
  ...restProps
}) => {
  const [currentSelectItem, setCurrentSelectItem] = useState('');
  const inputRef = useRef(null);
  const [inputError, setInputError] = useState(errorText);
  const [isListboxOpen, setListboxOpen] = useState(false);
  const [searchHasNoMatch, setSearchHasNoMatch] = useState(false);
  const { listboxItemRef, listboxRef } = useScrollToItem(currentSelectItem, optionsTemplate || {});

  const downshiftInternalActions = {};
  const actionList = ['clearSelection', 'closeMenu', 'openMenu', 'selectItem', 'setHighlightedIndex'];
  useComboboxAction(comboboxAction, downshiftInternalActions);

  //Error text from parent component
  useEffect(() => {
    if (errorText) {
      setInputError(errorText);
    } else {
      setInputError('');
    }
  }, [errorText]);

  useEffect(() => {
    if (inputError) {
      //if errorText exist, when noMatch error disappear, reset back to errorText
      if (errorText) {
        setInputError(errorText);
      } else {
        setInputError('');
      }
    }
    if (searchHasNoMatch) {
      setListboxOpen(false);
    } else {
      setListboxOpen(true);
    }
  }, [errorText, inputError, searchHasNoMatch]);

  const handleInputBlur = e => {
    typeof onInputBlur === 'function' && onInputBlur(e, e.target.value);
  };

  const handleInputChange = e => {
    typeof onInputChange === 'function' && onInputChange(e, e.target.value);
  };

  const handleInputFocus = e => {
    typeof onInputFocus === 'function' && onInputFocus(e, e.target.value);
  };

  const handleSelection = selection => {
    setCurrentSelectItem(selection ? selection : null);
    setInputError('');
    typeof onSelectionChange === 'function' && onSelectionChange(selection);
  };

  const handleNoMatch = noMatch => {
    if (searchHasNoMatch !== noMatch) {
      setSearchHasNoMatch(noMatch);
    }
  };

  return (
    <Downshift
      initialInputValue=""
      initialSelectedItem={currentSelectItem}
      itemToString={itemToStringFunc}
      onChange={handleSelection}
    >
      {({ getInputProps, isOpen, ...listboxDownshift }) => { // eslint-disable-line no-unused-vars
        if (Object.keys(downshiftInternalActions).length === 0) {
          downshiftInternalActions.setInputError = setInputError;
          for (const action of actionList) { // eslint-disable-line
            downshiftInternalActions[action] = listboxDownshift[action];
          }
        }
        return (
          <div {...containerProps} className={classnames('vds-combobox-container', comboboxClass.containerClass)}>
            <Input
              {...getInputProps({
                onBlur: handleInputBlur,
                onChange: handleInputChange,
                onFocus: handleInputFocus
              })}
              {...inputProps}
              className={classnames('vds-combobox-input-wrapper', comboboxClass.inputWrapperClass)}
              errorText={inputError}
              ref={inputRef}
              invalid={Boolean(inputError)}
              labelId={getInputProps()['aria-labelledby']}
              showErrorText={Boolean(inputError)}
              showHelpText={Boolean(inputProps.helpText)}
              type="text"
            />
            <Listbox
              {...listboxDownshift}
              {...restProps}
              listboxClass={comboboxClass.listboxClass}
              isListboxOpen={isListboxOpen}
              listboxItemRef={li => {
                listboxItemRef.current = li;
              }}
              listboxRef={ul => {
                listboxRef.current = ul;
              }}
              isOpen={isOpen}
              noMatchCallback={handleNoMatch}
              noSelectionCheckMark
              optionsTemplate={optionsTemplate}
            />
          </div>
        );
      }}
    </Downshift>
  );
};

ComboboxFreeformManualSelection.propTypes = {
  comboboxAction: func,
  comboboxClass: shape({
    containerClass: string,
    inputWrapperClass: string,
    listboxClass: string
  }),
  containerProps: shape({}),
  errorText: string,
  id: oneOfType([number, string]),
  inputProps: shape({
    disabled: bool,
    helpText: string,
    label: string.isRequired,
    name: string.isRequired,
    placeholder: string,
    required: bool,
    showHelpText: bool
  }),
  itemToStringFunc: func.isRequired,
  onInputBlur: func,
  onInputChange: func,
  onInputFocus: func,
  onSelectionChange: func,
  optionsTemplate: shape({
    itemKey: string.isRequired,
    optionValues: arrayOf(shape({ value: string.isRequired })),
    renderOption: func.isRequired,
    searchIncludeFields: arrayOf(string)
  })
};

ComboboxFreeformManualSelection.defaultProps = {
  comboboxClass: {
    containerClass: null,
    inputWrapperClass: null,
    listboxClass: null
  },
  errorText: '',
  inputProps: {
    label: 'Text for label',
    name: 'combobox'
  }
};

ComboboxFreeformManualSelection.displayName = 'ComboboxFreeformManualSelection';

export default ComboboxFreeformManualSelection;
