import { useEffect, useCallback } from 'react';
/**
 * @description Reverse of Control,
 * expose downshift internal action to parent component so
 * we can customize combobox behavior to meet different requirements
 *
 * @param {function} comboboxAction
 * @param {object} downshiftInternalActions
 */

const useComboboxAction = (comboboxAction, downshiftInternalActions) => {
  const dispatchComboboxAction = useCallback((action, data) => {
    try {
      if (data === undefined) {
        downshiftInternalActions[action.type]();
      } else {
        downshiftInternalActions[action.type](data);
      }
    } catch (err) {
      throw new Error(`Action ${action.type} is not available.`);
    }
  }, [downshiftInternalActions]);

  useEffect(() => {
    if (typeof comboboxAction === 'function') {
      comboboxAction(dispatchComboboxAction);
    }
  }, [comboboxAction, dispatchComboboxAction, downshiftInternalActions]);
};

export default useComboboxAction;
