import { useEffect, useRef } from 'react';
/**
 * @description custom react hook, if there is a long list of items,
 * this hook will scroll the selected item to the view
 *
 * @param {object} currentSelectItem
 * @param {object} optionsTemplate
 * @param {string} comboboxType
 * @returns {object} listboxItemRef, listboxRef
 */
const useScrollToItem = (currentSelectItem, optionsTemplate, comboboxType) => {
  const listboxItemRef = useRef(null);
  const listboxRef = useRef(null);
  useEffect(() => {
    if (listboxRef.current && listboxItemRef.current && comboboxType !== 'auto') {
      if (currentSelectItem && optionsTemplate && optionsTemplate.optionValues) {
        const index = optionsTemplate.optionValues.findIndex(
          item => item[optionsTemplate.itemKey] === currentSelectItem[optionsTemplate.itemKey]
        );
        if (index >= 0) {
          listboxRef.current.scrollTop += listboxItemRef.current.offsetTop;
        }
      }
    }
  });
  return { listboxItemRef, listboxRef };
};

export default useScrollToItem;
