/* eslint-disable no-console */

import React, { useEffect, useState } from 'react';
import ButtonGroup from '../../button-group';
import ButtonText from '../../button-text';
import Col from '../../col';
import ComboboxFreeformManualSelection from '../../combobox-freeform-manual-selection';
import Grid from '../../grid';
import Row from '../../row';
import Typography from '../../typography';

const ComboboxFreeformManualSelectionExample = () => {
  const [state, setState] = useState({
    disabled: false
  });
  const [service, setService] = useState();
  const [sessionToken, setSessionToken] = useState();
  const [placesService, setPlacesService] = useState();

  useEffect(() => {
    /*
     * Load google address suggestion JS API
     */
    if (typeof window.google === 'undefined' && document.getElementById('loadGoogleMapsAPI') === null) {
      const loadGoogleMapsAPI = document.createElement('script');
      loadGoogleMapsAPI.id = "loadGoogleMapsAPI";
      loadGoogleMapsAPI.async = "true";
      loadGoogleMapsAPI.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyCkX5pcWYEKgFndJpy2o6rWMNErtKyw6w8&libraries=places";
      document.querySelector('body').appendChild(loadGoogleMapsAPI);
    }

    const googleObject = setInterval(() => {
      if (typeof google !== 'undefined') {
        setService(new window.google.maps.places.AutocompleteService());
        setSessionToken(new window.google.maps.places.AutocompleteSessionToken());
        setPlacesService(new window.google.maps.places.PlacesService(document.createElement('div')));
        clearInterval(googleObject);
      }
    }, 10);
    // Ends Google Maps API loading
  }, []);

  const GoogleSuggestCombobox = () => {
    const suggestionObj = {
      city: '',
      line1: '',
      postalCode: '',
      stateProvinceCode: ''
    };
    const [optionsTemplate, setOptionsTemplate] = useState(null);

    const renderOption = optionItem => <span className="address-suggestion">{optionItem.value}</span>;

    const inputProps = {
      disabled: state.disabled,
      helpText : 'Address line 1',
      label: 'Address line 1',
      name: 'combobox'
    };

    let googlePredictions = [];

    const mapToField = components => {
      if (!components.length) {
        return;
      }

      let addressLine1Field = '';
      let cityField = '';
      let stateField = '';
      let zipField = '';

      components.forEach(address => {
        console.log('address :::: ', address);
        if (address.types.includes('street_number')) {
          addressLine1Field = address.long_name;
          suggestionObj.line1 = addressLine1Field;
        } else if (address.types.includes('route')) {
          addressLine1Field = (addressLine1Field) ? `${addressLine1Field} ${address.long_name}` : address.long_name;
          suggestionObj.line1 = addressLine1Field;
        } else if (address.types.includes('locality')) {
          cityField = address.long_name;
          suggestionObj.city = cityField;
        } else if (address.types.includes('administrative_area_level_1')) {
          stateField = address.short_name;
          suggestionObj.stateProvinceCode = stateField;
        } else if (address.types.includes('postal_code')) {
          zipField = address.long_name;
          suggestionObj.postalCode = zipField;
        }
      });

      console.log(`Address Line 1: ${addressLine1Field}`);
      console.log(`City: ${cityField}`);
      console.log(`State: ${stateField}`);
      console.log(`Zip: ${zipField}`);
      console.log(`Suggestion: ${JSON.stringify(suggestionObj)}`);
    };

    const onInputChange = e => {
      const val = e.target.value;
      const len = val.length;

      if (typeof service !== 'undefined' && len) {
        service.getPlacePredictions({
          input: val,
          sessionToken
        }, (predictions, status) => {
          if (status !== window.google.maps.places.PlacesServiceStatus.OK) {
            return;
          }

          googlePredictions = [];

          predictions.forEach(prediction => {
            googlePredictions.push({
              place_id: prediction.place_id,
              value: prediction.description
            });
          });

          setOptionsTemplate({
            itemKey: 'value',
            optionValues: googlePredictions,
            renderOption,
            searchIncludeFields: ['value']
          });
        });
      }
    };

    const onSelectionChange = ({ place_id }) => {
      if (place_id) {
        placesService.getDetails({
          fields: ['address_components'],
          placeId: place_id,
          sessionToken
        }, (place, status) => {
          if (status === window.google.maps.places.PlacesServiceStatus.OK) {
            mapToField(place.address_components);
          }
        });

        // sessionToken = new window.google.maps.places.AutocompleteSessionToken();
      }
    };

    const positionOfCommaThatFollowsTriggeringFieldAddressInfo = 0;
    const shortenedSelectedItemValueChar = ',';

    return (
      <React.Fragment>
        <Typography tag="h4">Google Address Example</Typography>
        <Typography>
          This is an example of the Autocomplete Manual
          Selection combobox. But it's a different flavor.
          The difference between this and the regular combobox
          is that the Freeform combobox should be used when you
          do not have any options on initial render; some type
          of user interaction is required.  The regular combobox
          requires a list of options on initial render.
        </Typography>
        <ComboboxFreeformManualSelection
          inputProps={inputProps}
          itemToStringFunc={item => (
            item
              ? item.value.split(shortenedSelectedItemValueChar)[positionOfCommaThatFollowsTriggeringFieldAddressInfo]
              : ''
          )}
          optionsTemplate={optionsTemplate}
          onInputChange={onInputChange}
          onSelectionChange={onSelectionChange}
        />
        <ButtonGroup direction="right" style={{ marginTop: "30px" }}>
          <ButtonText colorScheme="primary" onClick={() => setState({ disabled: false })}>Enable</ButtonText>
          <ButtonText colorScheme="secondary" onClick={() => setState({ disabled: true })}>Disable</ButtonText>
        </ButtonGroup>
      </React.Fragment>
    );
  };

  const ManualAutocompleteCombobox = () => {

    const renderOption = optionItem => <span className="card-name">{optionItem.value}</span>;

    const inputProps = {
      disabled: state.disabled,
      helpText : 'Manual Autocomplete',
      label: 'Manual Autocomplete',
      name: 'combobox'
    };

    const cardsValue = [
      { value: 'silver signature card' },
      { value: 'visa signature card' },
      { value: 'platinum visa card' },
      { value: 'premium card' },
      { value: 'another card' },
      { value: 'other card' },
      { value: 'secondary card' },
      { value: 'tertiary card' },
      { value: 'gold signature card' },
      { value: 'yet another card' },
      { value: 'last card' }
    ];

    const optionsTemplate = {
      itemKey: 'value',
      optionValues: cardsValue,
      renderOption,
      searchIncludeFields: ['value']
    };

    const onInputChange = e => console.log('onInputChange', e.target.value);
    const onSelectionChange = item => console.log('onSelectionChange', JSON.stringify(item));

    return (
      <React.Fragment>
        <ComboboxFreeformManualSelection
          optionsTemplate={optionsTemplate}
          inputProps={inputProps}
          itemToStringFunc={item => (item ? item.value : '')}
          onInputChange={onInputChange}
          onSelectionChange={onSelectionChange}
        />
        <ButtonGroup direction="right" style={{ marginTop: "30px" }}>
          <ButtonText colorScheme="primary" onClick={() => setState({ disabled: false })}>Enable</ButtonText>
          <ButtonText colorScheme="secondary" onClick={() => setState({ disabled: true })}>Disable</ButtonText>
        </ButtonGroup>
      </React.Fragment>
    );
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Combobox Freeform Manual Selection</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <GoogleSuggestCombobox />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Generic Example</Typography>
          <Typography>
            This is an example of the Autocomplete Manual
            Selection combobox. But it's a different flavor.
            The difference between this and the regular combobox is
            that the Freeform combobox should be used when you
            do not have any options on initial render; some type of user
            interaction is required.  The regular combobox requires a list
            of options on initial render.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <ManualAutocompleteCombobox />
        </Col>
      </Row>
    </Grid>
  );
};

export default ComboboxFreeformManualSelectionExample;
